.cont {
  background: #222b56;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 0;
  overflow: hidden;
}

#overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #4c484729;
  opacity: 0.8;
  z-index: 1;
}

.login-cont {
  min-width: 30%;
  box-sizing: border-box;
  border: 1px solid #252e5b;
  margin: 0;
  padding: 10px 0;
  background: #fff;
  z-index: 2;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
}

.login-txt {
  font-size: 40px;
  color: #1d2f88;
  text-align: center;
  padding: 10px;
}

.loginFormContainer {
  width: 100%;
  padding: 30px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  line-height: 2;
}

.email-cont, .pwd-cont {
  width: 100%;
  align-items: center;
  margin: 5px;
}

input.input-style {
  text-align: center;
}


.email-cont, .pwd-cont {
  display: flex;
  flex-direction: column;
}

input.login-input {
  font-size: 16px;
  text-align: center;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  background: none;
  outline: none;
  width: 90%;
  color: #9E9E9E;
  padding: 5px 10px 10px 5px;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #36407c;
}

.transform-text {
  text-transform: lowercase;
}

.label-col {
  color: #36407c;
  font-size: 14px;
  font-weight: bolder;
  font-family: 'Comfortaa', cursive;
}

.login-sub {
  background: #121a42;
  color: #fff;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 20px;
  padding: 5px 25px;
  border: 1px solid #fffcfcfa;
  min-width: 40%;
}

.hve-acct {
  color: #36407c;
  font-family: 'Comfortaa', cursive;
  display: flex;
  align-items: center;
  padding: 0 30px;
}

.signupLink {
  background: #fff;
  padding: 5px 10px;
  color: #28285d;
  font-family: 'Comfortaa', cursive;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  margin-left: 5px;
  border: 1px solid #36407c;
}

.signupLink:hover {
  text-decoration: underline;
  background: #252e5b;
  color: #fff
}

.loginErr {
  color: #cc0000;
  padding: 0 30px;
  font-family: 'Comfortaa', cursive;
  text-align: center;
}

@media (min-width: 320px) and (max-width: 480px) {
  .hve-acct, .signupLink {
    font-size: 14px;
  }

  .signupLink {
    font-size: 20px;
  }

  .login-cont {
    width: 100%;
    height: 100vh;
    background: #121a42;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .login-txt {
    color: #fff;
  }

  .login-sub {
    font-size: 25px;
  }

  .cont {
    display: block;
    height: 100vh;
    overflow: scroll;
  }

  #overlay {
    display: none;
  }

  .label-col, .hve-acct {
    color: #fff;
  }

  .loginErr {
    text-align: center;

  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .hve-acct, .signupLink {
    font-size: 14px;
  }

  .signupLink {
    font-size: 20px;
  }

  .login-cont {
    width: 100%;
    height: 100vh;
    background: #121a42;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .login-txt {
    color: #fff;
  }

  .cont {
    display: block;
    height: 100vh;
    overflow: scroll;
  }

  #overlay {
    display: none;
  }

  .label-col, .hve-acct {
    color: #fff;
  }

  .loginErr {
    text-align: center;

  }
}
.body {
  background: #fff;
}

.sweetAlert {
  font-size: 12px;
}

.unauthorized {
  width: 100%;
}

.loading {
  text-align: center;
}

.delete {
  padding: 5px;
  border-radius: 10px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #fff;
  color:#fff;
  background: #c00404;
  cursor: pointer;
  outline: none;
  font-family: 'Comfortaa', cursive;
}

.cancel {
  padding: 5px;
  border-radius: 10px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #fff;
  color:#fff;
  background: #121a42;
  cursor: pointer;
  outline: none;
  font-family: 'Comfortaa', cursive;
}

.articles {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7%;
}

.cont-art {
  width: 70%;
}

.postArticles {
  width: 100%;
}


.homeHeader {
  background: #121a42;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1;
}

.homeHeader a {
  overflow: hidden;
  padding: 5px 25px;
  font-family: monospace;
  font-size: 14px;
  border: 1px solid #fff;
  color:#fff;
  background-image: -webkit-linear-gradient(left, #fff 50%, #121a42 50%);
  background-image: linear-gradient(to right, #fff 50%, #121a42 50%);
  background-size: 200% 100%;
  background-repeat: no-repeat;
  background-position: bottom right;
  -webkit-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
  outline: none;
  text-decoration: none;
  text-transform: capitalize;
}

.homeHeader a:hover {
  -webkit-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
  background-position: bottom left;
  color: #121a42;
}

.nav-head {
  font-size: 30px;
  font-family: 'Comfortaa', cursive;
  color: #121a42;
}

form.pst-article {
  background: #121a42;
  padding: 15px;
}

.pst-art-title, .pst-art-subheading, .pst-art-imgUrl, .pst-art-lparag, .pst-art-body {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  color: #fff;
}

label.titleLb, label.subhLb, label.paragLb, label.bodyLb, label.imgLb {
  color: #fff;
  padding: 5px 0;
}

input.title, input.subheading, input.img-url {
  font-size: 14px;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  background: #fff;
  outline: none;
  color: #121a42;
  padding: 5px 10px;
}

textarea.leadParagraph, textarea.body {
  box-sizing: border-box;
  background: #fff;
  font-family: 'Comfortaa', cursive;
  font-size: 14px;
  width: auto;
  color: #121a42;
  border-radius: 0;
  resize: none;
  outline: none;
  border: 1px solid;
  height: 150px;
  padding: 5px;
}

button.sub-art {
  text-align: center;
  padding: 8px 25px;
  font-size: 20px;
  border: 1px solid #fff;
  color:#fff;
  background: #121a42;
  -webkit-transition: all 0.25s ease-in;
  transition: all 0.25s ease-in;
  cursor: pointer;
  outline: none;
  font-family: 'Comfortaa', cursive;
  width: 40%;
}

.initialArtDisplay {
  color: #213ad6;
  font-family: 'Comfortaa', cursive;
  display: flex;
  line-height: 2;
  text-transform: none;
  text-transform: initial;
  flex-direction: column;
  width: 100%;
}

.readMore {
  color: #878a8e;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  font-size: 12px;
  text-transform: lowercase;
}

.readMore:hover {
  text-decoration: underline;
}

.articleBody, .articleForm-edit {
  color: #121a42;
  font-family: 'Roboto', sans-serif;
  line-height: 1;
  text-transform: none;
  background: #63696912;
  padding: 15px;
  position: relative;
  margin: 3px 0;
}

.articleBody p {
  font-size: 14px;
  font-family: sans-serif;
  color: #121a42;
}

.articleBody h2 {
  font-size: 30px;
}

.articleBody h3 {
  font-size: 20px;
  font-family: 'Comfortaa', cursive;
}

p.author, p.date-published {
  font-size: 10px;
  color: #847e7e;
}

.article-save-btn, .article-edit-btn {
  border-radius: 5px;
  padding: 10px 20px;
  color: #fff;
  margin: 1px;
  background: #121a42;
  cursor: pointer;
  outline: none;
  font-size: 14px;
  font-family: 'Comfortaa', cursive;
  border: none;
}

.article-delete-btn, .article-cancel-btn, .article-close-btn {
  border-radius: 5px;
  padding: 10px 20px;
  color: #fff;
  margin: 1px;
  background: #9e190f;
  cursor: pointer;
  outline: none;
  font-size: 14px;
  font-family: 'Comfortaa', cursive;
  border: none;
}

.article-close-btn {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: inherit;
  background: #f4f4f4;
  color: #f1aeae;
  font-size: 20px;
  padding: 5px;
}

.article-close-btn:hover {
  color: #ff3a11;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
  -webkit-text-fill-color: #36407c !important;
}

.err-msg {
  color: #cc0000;
  font-family: 'Comfortaa', cursive;
  text-align: center;
  padding-bottom: 10px;
}

/* form edit div */
.form-edit  {
  display: flex;
  flex-direction: column;
  margin: 2% 0;
}

.label {
  color: #af2f2f;
  font-size: 14px;
  padding: 5px;
  padding: 5px 0;
}

.art-lists-title {
  font-size: 16px;
  text-transform: capitalize;
  border-top: 1px solid #b4b5bd3d;
  padding: 5px 0;
  margin: 0;
}

.logout {
    background-repeat: no-repeat;
    background-position: center;
    outline: none;
    cursor: pointer;
    border: none;
    width: 30px;
    height: auto;
    -webkit-transition: -webkit-transform 1.5s ease-in-out;
    transition: -webkit-transform 1.5s ease-in-out;
    transition: transform 1.5s ease-in-out;
    transition: transform 1.5s ease-in-out, -webkit-transform 1.5s ease-in-out;
    opacity: 0.5;
}

.logout:hover {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  opacity: 0.8;

}

.home-title {
  padding: 5px 25px;
  font-size: 14px;
  border: 1px solid #fff;
  color:#121a42;
  background: #fff;
  outline: none;
  cursor: pointer;
}

input.input-col.input-edit {
  width: 100%;
}

