  .pf-cont {
    margin-top: 15%;
    padding: 0;
    text-align: center;
  }

  .nav-head {
    padding: 0;
  }

  .profileFormEdit {
    background: #121a42;
    border-radius: 8px;
    padding: 6px 0;
  }

  .profileBody, .profileFormEdit {
    display: flex;
    padding: 0;
    flex-direction: column;
    width: 100%;
    margin: 0 auto;
  }

  .profiles-div {
    padding: 2px 5px;
  }

  .pf-btn {
    margin-top: 3%;
  }

  .articlesHeader, .no-art {
    font-size: 20px;
  }

  .form-edit {
    padding: 4px;
  }

  .label {
    color: #fff;
    padding: 5px;
  }

  input.input-col {
    text-align: center;
    border: none;
    border-bottom: 1px solid #121a42;
    padding: 15px;
  }

  .btn-wrap, .pf-btn {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .profileFormEdit button, .pf-btn button {
    box-shadow: none;
  }

  button.save-btn {
    background: #fff;
    text-decoration: underline;
    color: #121a42;
    font-weight: bolder;
  }

  .profileArtBody {
    width: 96%;
    padding: 6px;
    text-align: left;
    margin: 5px auto;
  }

  .profileArtBody h2 {
    font-size: 18px;
  }

  .profileArtBody p {
    font-size: 14px;
  }
}
body {
  margin: 0 0;
  height: 100%
}

.container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 30px;
  background: #121a42;
  color: #fff;
}

.logo, .foot-logo {
  font-family: 'Covered By Your Grace', cursive;
  font-size: 30px;
  color: #9ed2ff;
}

.getstarted {
  background: #ffffff;
  padding: 5px 10px;
  color: #121a42;
  font-family: 'Comfortaa', cursive;
  display: flex;
  align-items: center;
}

.loginLink {
  background: #121a42;
  padding: 5px 10px;
  color: #fff;
  font-family: 'Comfortaa', cursive;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 14px;
}

.loginLink:hover {
  color: #28285d;
  background: #d9e8f5
}

.section1 {
  background: #9237650a;
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.sectionContent {
  text-align: center;
  margin: 0;
  line-height: 1.6;
}

.move {
  font-size: 35px;
  color: #28285d;
  margin-top: 0;
  margin-bottom: 0;
}

.create {
  font-family: 'Comfortaa', cursive;
  margin-top: 0;
  margin-bottom: 0;
}

.create-acc {
  background: #121a42;
  padding: 8px 10px;
  color: #fff;
  font-family: 'Comfortaa', cursive;
  cursor: pointer;
  outline: none;
  font-size: 18px;
  border: none;
}

.create-acc:hover {
  color: #28285d;
  background: #f7fbff71
}

footer {
  font-family: 'Comfortaa', cursive;
  background: #121a42;
  letter-spacing: 0.1em;
  height: 33vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  left: 0;
  right: 0;
}

.footerText {
  text-align: center;
  color: #fff;
}

img {
  width: 200px;
  height: 204px;
}


/* portrait mobile */
@media (min-width: 320px) and (max-width: 480px) {
  .getstarted {
    font-size: 12px;
    padding: 2px;
  }

  .sectionContent {
    line-height: 1.8;
  }

  .move {
    font-size: 20px;
  }

  .footerText {
    font-size: 12px;
    padding: 5px;
    margin-bottom: 15%;
  }

  .foot-logo {
    font-size: 18px;
  }

  .container {
    overflow: scroll;
  }
}


/* landscape mobile */
@media (min-width: 481px) and (max-width: 767px) {
  .container {
    overflow: scroll;
  }

  .getstarted {
    font-size: 10px;
    padding: 2px;
  }

  .sectionContent {
    line-height: 1.8;
  }

  .move {
    font-size: 20px;
  }

  .footerText {
    font-size: 12px;
    padding: 5px;
    margin-bottom: 5%;
  }

  .foot-logo {
    font-size: 18px;
  }
}
.signUp {
  border: 1px solid #121a42;
  min-width: 38%;
  background: #ffffff;
  position: absolute;
  top: 10%;
  z-index: 1;
  left: 30%;
}

.signup-txt {
  font-size: 40px;
  color: #121a42;
  text-align: center;
  margin: 10px 0;
}

.signupFormContainer {
  margin: 0;
}

.nameContainer, .emailContainer, .bioContainer, .passwordContainer {
  display: flex;
  flex-direction: column;
  padding: 0 30px;
}

input.signup-input, textarea.signup-input {
  font-size: 16px;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  background: none;
  outline: none;
  width: 100%;
  color: #36417c;
  padding: 5px 10px 10px 5px;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #36407c;
  resize: none;
}

label {
  color: #121a42;
  font-size: 14px;
  font-weight: bolder;
  font-family: 'Comfortaa', cursive;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #E9E9FD inset;
  -webkit-text-fill-color: #013ef5 !important;
}

.signup-Submit {
  background: #121a42;
  color: #fff;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 20px;
  margin-left: 30px;
  padding: 5px 25px;
  border: none;
  min-width: 40%;
}

.hve-acc {
  padding: 0 30px;
  color: #121a42;
  font-family: 'Comfortaa', cursive;
  display: flex;
  align-items: center;
}

.loginLink {
  background: #fff;
  padding: 5px 10px;
  color: #28285d;
  font-family: 'Comfortaa', cursive;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-size: 14px;
  margin-left: 5px;
  border: 1px solid #121a42;
}

.loginLink:hover {
  text-decoration: underline;
  background: #252e5b;
  color: #fff
}

.succ-msg {
  color: rgba(0, 165, 22, 0.829);
  padding: 0 30px;
  font-family: 'Comfortaa', cursive;
}

.topright {
  position: absolute;
  top: 0;
  font-size: 25px;
  cursor: pointer;
  right: 0;
  outline: none;
  border: 1px solid #121a42;
  border-top: none;
  border-right: none;
}

.topright:hover {
  color: #ff0b0b;
  background: #121a42;
}

@media (min-width: 320px) and (max-width: 480px) {
  .signUp {
    width: 96%;
    left: 5px;
    background: #dde3fff5;
  }

  .signupFormContainer {
    margin: 0;
    padding: 10px;
}
  
  .signup-txt {
    font-size: 25px;
  }

  .loginLink, .hve-acc, .signup-Submit {
    font-size: 14px;
  }

  .signup-Submit {
    font-size: 25px;
    outline: none;
    border: none;
    border: 1px solid #121a42;
    margin: 0;

  }

  textarea {
    padding: 0;
  }

  .err-msg {
    padding: 0;
    font-size: 15px;
    font-weight: bolder;
  }

  .nameContainer, .emailContainer, .bioContainer, .passwordContainer {
    padding: 0;
  }

  .input, input.input {
    width: 100%;
  }

  .topright {
    color: #ff0b0b;
  }
}
@media (min-width: 481px) and (max-width: 767px) {
  .signUp {
    background: #dde3fff5;
  }

  .topright {
    color: #ff0b0b;
  }
}
