.writer-cont {
  margin: 0 auto; 
  width: 70%;
  margin-top: 7%;
}
 
 .writerBody {
  font-family: 'Roboto', sans-serif;
  line-height: 1;
  text-transform: none;
  background: #121a42;
  padding: 15px;
  margin: 1px;
}
  
.writers {
  display: flex;
  align-items: baseline;
}

.writer-label {
  font-size: 18px;
  margin-right: 10px;
  color: #9ed2ff;
}

.writerBody span {
  font-size: 14px;
  font-family: monospace;
  color: #fff;
  margin: 0;
  line-height: 2;
  font-weight: 200;
}

.art-count button {
  background: #253658;
  color: #5da6e9;
  border-radius: 10px;
  margin-right: 5px;
  outline: none;
  cursor: pointer;
}

p.art-count {
  display: flex;
  align-items: center;
  color: grey;
  cursor: pointer;
  transition: 0.2s ease-in;
}

p.art-count:hover {
  color: floralwhite;
}

.title-click {
  color: #ffe4c491;
  text-transform: capitalize;
  font-size: 14px;
  -webkit-text-emphasis-style: triangle;
  -webkit-text-emphasis-color: #9ed3fb94;
}

.writr-title {
  padding: 5px 25px;
  font-size: 14px;
  border: 1px solid #fff;
  color:#25335a;
  background: #fff;
  outline: none;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 480px) {
  .writer-cont {
    margin-top: 14%;
    width: 100%;
  }

  .writerBody {
    border-radius: 5px
  }
}